@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    font-family: 'Source Code Pro', serif;
    font-size: 16px;
    color: lightgray;

    background-color: olivedrab;
    border-bottom: solid 2.5px black; 

    margin: 0;
    padding: 1.5em;

    top: 0;
    width: 100vw;
}

header h1 {
    font-size: 2em;
    font-weight: 400;

    text-shadow: 1px 1px 3px black;
    padding: 5px;
}

header form {
    display: flex;
    flex-direction: row;
}

.create-link-button {
    border: 1px solid black;
    box-shadow: 5px 5px 3px black;
}
.link-header {
    text-align: center;
    font-style: 1.65rem;
    color: lightgray;
    text-shadow: 1px 1px 3px black;

    background-color: olivedrab;
    border: 1px solid black;
}

.link-list-item {
    border: 1px solid darkgrey;
    background-color: whitesmoke;
    padding: 2.5px;
    font-size: 1.05rem;
}

.link-list-item:last-of-type {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.update-button {
    height: 1.75rem;
    font-size: .75rem;
    background-color: darkgrey;
    border: 1px solid black;
    color: black;
    box-shadow: 1px 1px 3px black;
}
.modal-header {
    background-color: olivedrab;
    color: lightgray;
    text-shadow: 1px 1px 3px black;
}
